@import './styles.colors.scss';

.btn {
    height: 32px;
    padding: 8px;
    border-radius: 80px;
    border-width: 1px;
    border-style: solid;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    direction: rtl;
    text-align: center;
    user-select: none;
    cursor: pointer;

    &.two-sides {
        justify-content: space-between;
    }

    &.medium {
        height: 40px;
        border-radius: 80px;
        padding: 11.5px;
    }
    &.large {
        height: 50px;
        padding: 16.5px;
    }
    &.disable {
        opacity: 0.4;
    }
    &.stretch {
        width: 100%;
        box-sizing: border-box;
    }
}

.dark {
    &.fill {
        border-color: $black;
        background-color: $black;
        color: $oil01;
    }
    &.outline {
        border-color: $black;
        color: $black;
        background-color: transparent;
    }
    &.text {
        border: none !important;
        color: $black;
        background-color: transparent;
        text-decoration: underline;
    }
}

.infra-red {
    &.outline {
        border-color: $infraRed06;
        color: $infraRed06;
        background-color: $white;
    }
    &.fill {
        border-color: $infraRed06;
        color: $white;
        background-color: $infraRed06;
    }
}

.oil {
    &.fill {
        border-color: $oil04;
        background-color: $oil04;
        color: $oil01;
    }
    &.outline {
        border-color: $oil10;
        color: $oil08;
        background-color: transparent;
    }
    &.text {
        border: none !important;
        color: $oil08;
        background-color: transparent;
        text-decoration: underline;
    }
}

.sun {
    &.outline {
        border-color: $sun06;
        color: $sun06;
        background-color: transparent;
    }
}