@import './styles.colors.scss';

@font-face {
    font-family: "IranYekanLight";
    src: url('/assets/fonts/IRANYekanMobileLight.ttf') format("truetype"),
        url('/assets/fonts/iranyekanweblightfanum.woff') format("woff");
}

@font-face {
    font-family: "IranYekanRegular";
    src: url('/assets/fonts/IRANYekanMobileRegular.ttf') format("truetype"),
        url('/assets/fonts/iranyekanwebregularfanum.woff') format("woff");
}

@font-face {
    font-family: "IranYekanBold";
    src: url('/assets/fonts/IRANYekanMobileBold.ttf') format("truetype"),
        url('/assets/fonts/iranyekanwebboldfanum.woff') format("woff");
}

.t-black { color: $oil10 }
.t-dim { color: $oil08 }
.t-disable { color: $oil06 }
.t-error { color: $infraRed06 }
.t-white { color: $white }
.t-oil1 { color: $oil01 }

.t-light { font-family: IranYekanLight; font-weight: 300; }
.t-regular { font-family: IranYekanRegular; font-weight: 400; }
.t-bold { font-family: IranYekanBold; font-weight: 700; }

.t48 { font-size: 50px }
.t32 { font-size: 34px }
.t24 { font-size: 26px }
.t18 { font-size: 20px }
.t16 { font-size: 18px }
.t14{ font-size: 16px }
.t12{ font-size: 14px }
.t10{ font-size: 12px }

.bold-14-black { @extend .t-bold; @extend .t14; @extend .t-black; }
.regular-14-black { @extend .t-regular; @extend .t14; @extend .t-black; }
.regular-14-disable { @extend .t-regular; @extend .t14; @extend .t-disable; }
.light-12-disable { @extend .t-light; @extend .t12; @extend .t-disable; }