@import './styles.button.scss';
@import './styles.fonts.scss';
@import './styles.spaces.scss';

/* You can add global styles to this file, and also import other style files */
$fontFamily1: Iransans;

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
}
body {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    background: none;
    overflow: hidden;
}
.hide {
    display: none !important;
}
.unseen {
    visibility: hidden;
    position: absolute;
    width: 0px;
    height: 0px;
    top: 0;
    left: 0;
}
.no-padding {
    padding: 0 !important;
}